import styled from "@emotion/styled";
import {scalePx} from "../../utils/scalePx";
import {useSizeRatio} from "../../hooks/useSizeRatio";
import {useProgress} from "../../hooks/useProgress";
import {QuestionLayout} from "../shared/QuestionLayout";
import {PersonConstructor} from "../PersonConstructor";

const PersonConstructorWrapper = styled.div`
    display: flex;
    width: 100%;
    max-height: 100%;
    padding: ${({sizeRatio}) => `${scalePx(68, sizeRatio)} ${scalePx(27, sizeRatio)} ${scalePx(14, sizeRatio)}`};
`;

export function SkillsSelector() {
    const sizeRatio = useSizeRatio();
    const {skills, setSkills, next} = useProgress();

    const handleNext = () => {
        if (skills?.length) {
            next();
        }
    };

    const title = '    \xa0      . \xa0- \xa0 , \xa0\xa0? :)   ,   .';

    return (
        <QuestionLayout
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            exit={{ opacity: 0 }}
            title={title}
            size="s"
            value={skills}
            options={[
                {value: 'communication', label: ''},
                {value: 'teamwork', label: ' '},
                {value: 'time-management', label: '-'},
                {value: 'self-organization', label: ''},
                {value: 'attentiveness', label: ''},
            ]}
            multi
            onChange={setSkills}
            onSubmit={handleNext}
        >
            <PersonConstructorWrapper sizeRatio={sizeRatio}>
                <PersonConstructor />
            </PersonConstructorWrapper>
        </QuestionLayout>
    )
}